import { Modal, Popover, DatePicker, Space } from "antd";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import workApplyApi from "../../../../../api/workApplyApi";
import "../../../../scss/inforCompany/UserApply.scss";
import SpinLoad from "../../../Spin/Spin";
import moment from "moment";
import sendMailApi from "../../../../../api/sendMail";
export default function UserApply({ id }) {
  const [data, setData] = useState();
  console.log("data", data);
  const [numReload, setNumReload] = useState(1);
  const [state, setState] = useState({
    isModalUserVisible: false,
    titleModal: "",
    date: new Date(),
    textSendMail: "",
    email: "",
    userId: "",
    workId: "",
  });

  const {
    isModalUserVisible,
    titleModal,
    date,
    textSendMail,
    email,
    userId,
    workId,
  } = state;

  const getApi = async () => {
    await workApplyApi.checkWorkApply(id).then((data) => {
      setData(data.Works);
    });
  };

  const handleOk = () => {
    setState({ ...state, isModalUserVisible: false });
    sendMailApi.Send({
      email,
      textSendMail,
      sechedule: moment(date).format("DD/MM/YYYY"),
    });
    workApplyApi
      .editworkApply({ userId, workId, sechedule: date })
      .then((data) => {
        setNumReload((prev) => prev + 1);
      });
  };

  const handleCancel = () => {
    setState({ ...state, isModalUserVisible: false });
  };

  const handleClickContact = (name, email, userId, workId) => {
    setState({
      ...state,
      isModalUserVisible: true,
      titleModal: `Bn ang lin h vi ng vin ${name}`,
      email,
      userId,
      workId,
    });
  };

  const onChangeDate = (date, dateString) => {
    if (dateString) {
      setState({
        ...state,
        date: dateString,
      });
    }
  };

  const handleOnchaneTextSendMail = (e) => {
    const { value } = e.target;
    setState({
      ...state,
      textSendMail: value,
    });
  };

  useEffect(() => {
    getApi();
  }, [numReload]);

  let styleTextarea = {
    width: "100%",
    resize: "none",
    borderRadius: "6px",
    padding: "10px 20px",
  };
  return (
    <div className="userApply">
      <div className="heading">
        <div className="heading__title">
          <h3>ng vin ng tuyn</h3>
        </div>
        <div className="heading__hr"></div>
      </div>

      <div className="content">
        {!data ? (
          <SpinLoad />
        ) : (
          data.map((ok, index) => (
            <div className="content___box" key={index}>
              <div className="content___box--title">
                <Link to={`jobs/work/${ok.id}`} className="text-dark">
                  {ok.name}
                </Link>
              </div>
              <div className="hr"></div>
              <div className="content___box---user">
                <div className="row">
                  {ok.workapply2.length === 0 ? (
                    <p className="text-danger">Cha c ng vin ng tuyn</p>
                  ) : (
                    ok.workapply2.map((oki, index) => (
                      <div className="col-md-12" key={index}>
                        <div className="d-flex">
                          <div className="content___box---user---img">
                            <img
                              src={oki.avatar}
                              title={oki.name}
                              width={150}
                            />
                          </div>
                          <div className="content___box---user---infor position-relative">
                            <table>
                              <tbody>
                                <tr>
                                  <td className="td">Tn ngi dng</td>
                                  <td>
                                    <Link to={`candidates/${oki.id}`}>
                                      {oki.name}
                                    </Link>
                                  </td>
                                </tr>
                                <tr>
                                  <td className="td">a ch</td>
                                  <td>{oki.address}</td>
                                </tr>
                                <tr>
                                  <td className="td">Email</td>
                                  <td>{oki.email}</td>
                                </tr>
                                <tr>
                                  <td className="td">in thoi</td>
                                  <td>{oki.phone}</td>
                                </tr>
                                <tr>
                                  <td className="td">Gii tnh</td>
                                  <td>{oki.male}</td>
                                </tr>
                                {oki.WorkApplies.sechedule && (
                                  <tr>
                                    <td className="td">Lch phng vn</td>
                                    <td>
                                      {moment(oki.WorkApplies.sechedule).format(
                                        "DD/MM/yyyy",
                                      )}
                                    </td>
                                  </tr>
                                )}
                              </tbody>
                            </table>
                            <div className="btn-userApply">
                              {oki.WorkApplies.link && (
                                <button
                                  className="btn-link"
                                  onClick={() => {
                                    window.open(oki.WorkApplies.link);
                                  }}
                                >
                                  Xem CV
                                </button>
                              )}
                              {!oki.WorkApplies.sechedule && (
                                <button
                                  className="btn-link"
                                  onClick={() =>
                                    handleClickContact(
                                      oki.name,
                                      oki.email,
                                      oki.id,
                                      ok.id,
                                    )
                                  }
                                >
                                  Lin h ngay
                                </button>
                              )}
                            </div>
                            <Modal
                              title={titleModal}
                              visible={isModalUserVisible}
                              onOk={handleOk}
                              onCancel={handleCancel}
                            >
                              <p>
                                Lch phng vn:{" "}
                                <Space direction="vertical" className="w-100">
                                  <DatePicker
                                    onChange={onChangeDate}
                                    className="form-control input-ant"
                                    value={moment(
                                      date ?? new Date(),
                                      "YYYY-MM-DD",
                                    )}
                                  />
                                </Space>
                              </p>
                              <p>Li nhn:</p>
                              <textarea
                                className="box-textarea"
                                name=""
                                placeholder="in cc thng tin ng tuyn cho ng vin v ng qun lch phng vn c th"
                                value={textSendMail}
                                onChange={handleOnchaneTextSendMail}
                                rows="11"
                                style={styleTextarea}
                              ></textarea>
                            </Modal>

                            <Popover
                              content={oki.WorkApplies.message}
                              title="Li nhn"
                            >
                              <button className="btn-message">
                                <i className="fas fa-comment-dots"></i>
                              </button>
                            </Popover>
                          </div>
                        </div>
                      </div>
                    ))
                  )}
                </div>
              </div>
            </div>
          ))
        )}
      </div>
    </div>
  );
}
