import { message } from "antd";
import axiosClient from "./axiosClient";

class SaveWorkApi {
    getAll = (params) => {
        const url = '/saveWorks';
        return axiosClient.get(url, { params });
    };
    getOne = (params) => {
        const url = `/saveWorks/${params}`;
        return axiosClient.get(url).then(data => {
            return data.data
        });
    };
    postsaveWork = (params) => {
        const url = '/saveWorks';
        return axiosClient.post(url, params).then(data => {
            message.success(" lu cng vic!");
        }).catch(err => {
            message.error("C li xy ra!");
        });
    };
    deletesaveWork = (id) => {
        const url = `/deleteSaveWorks/${id}`;
        return axiosClient.delete(url).then(data => {
            message.success("Hu lu cng vic!");
        }).catch(err => {
            message.error("C li xy ra!");
        });
    };
    editsaveWork = (params) => {
        const url = `/saveWorks/${params.id}`;
        return axiosClient.patch(url, params).then(data => {
            message.success("Sa thnh cng!");
        }).catch(err => {
            message.error("C li xy ra!");
        });
    }
}
const saveWorkApi = new SaveWorkApi();
export default saveWorkApi;