import { message } from "antd";
import axiosClient from "./axiosClient";

class TypeWorkApi {
    getAll = (params) => {
        const url = '/typeWorks';
        return axiosClient.get(url, { params });
    };
    getOne = (params) => {
        const url = `/typeWorks/${params}`;
        return axiosClient.get(url).then(data => {
            return data.data
        });
    };
    posttypeWork = (params) => {
        const url = '/typeWorks';
        return axiosClient.post(url, params).then(data => {
            message.success("Thm thnh cng!");
        }).catch(err => {
            message.error("C li xy ra!");
        });
    };
    deletetypeWork = (id) => {
        const url = `/typeWorks/${id}`;
        return axiosClient.delete(url).then(data => {
            message.success("Xo thnh cng!");
        }).catch(err => {
            message.error("C li xy ra!");
        });
    };
    edittypeWork = (params) => {
        const url = `/typeWorks/${params.id}`;
        return axiosClient.patch(url, params).then(data => {
            message.success("Sa thnh cng!");
        }).catch(err => {
            message.error("C li xy ra!");
        });
    }
}
const typeWorkApi = new TypeWorkApi();
export default typeWorkApi;