import { Checkbox, message } from "antd";
import React from "react";
import { useForm } from "react-hook-form";
import { Link, useHistory } from "react-router-dom";
import "../../scss/Login/Login.scss";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import loginApi from "../../../api/loginApi";
export default function Login({ onLogin }) {
    const schema = yup.object().shape({
        userName: yup.string().email().required(),
        password: yup.string().min(4).max(20).required(),
    });
    const {
        register,
        handleSubmit,
        formState: { errors },
    } = useForm({
        resolver: yupResolver(schema),
    });
    const history = useHistory();
    const onSumit = async (data) => {
        await loginApi
            .loginCompany({
                email: data.userName,
                password: data.password,
                status: 1,
            })
            .then((ok) => {
                if (ok === "err") {
                    loginApi
                        .loginUser({
                            email: data.userName,
                            password: data.password,
                            status: 1,
                        })
                        .then((ok) => {
                            if (ok === "err") {
                                message.error("Sai tn ng nhp hoc mt khu!");
                            } else {
                                localStorage.setItem("token", ok);
                                message.success("ng nhp thnh cng!");
                                onLogin();
                                history.push("/");
                            }
                        });
                } else {
                    localStorage.setItem("token", ok);
                    message.success("ng nhp thnh cng!");
                    onLogin();
                    history.push("/");
                }
            });
    };
    return (
        <div className="login">
            <div className="login__title">Vic lm tt</div>
            <div className="login__box">
                <div className="line__login"></div>
                <div className="login__box__left">
                    <form onSubmit={handleSubmit(onSumit)}>
                        <div className="login__box__left__title">Ti khon</div>
                        <input type="text" {...register("userName")} placeholder="Email" />
                        <p className="text-danger">
                            {errors.userName ? "Email khng ng nh dng" : ""}
                        </p>
                        <div className="login__box__left__title">Mt khu</div>
                        <input
                            type="password"
                            {...register("password")}
                            placeholder="Mt khu"
                        />
                        <p className="text-danger">
                            {errors.password
                                ? "Mt khu t nht 4 k t v khng qu 20 k t"
                                : ""}
                        </p>
                        <Checkbox>Nh mt khu</Checkbox>
                        <div className="login__box__left__button">
                            <input type="submit" value="ng nhp" />
                        </div>
                    </form>
                </div>
                <div className="login__box__right">
                    <div className="right">
                        <div className="login__box__right__text">Hoc ng nhp vi</div>
                        <button className="fb">ng nhp vi facebook</button>
                        <button className="in">ng nhp vi instagram</button>
                        <div className="login__box__right__text">
                            Cha c ti khon? <Link to="/register">ng k</Link>  y
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
