import { message } from "antd";
import axiosClient from "./axiosClient";

class UserRoleApi {
    getAll = (params) => {
        const url = '/userRoles';
        return axiosClient.get(url, { params });
    };
    getOne = (params) => {
        const url = `/userRoles/${params}`;
        return axiosClient.get(url).then(data => {
            return data.data
        });
    };
    postuserRole = (params) => {
        const url = '/userRoles';
        return axiosClient.post(url, params).then(data => {
            message.success("Thm thnh cng!");
        }).catch(err => {
            message.error("C li xy ra!");
        });
    };
    deleteuserRole = (id) => {
        const url = `/userRoles/${id}`;
        return axiosClient.delete(url).then(data => {
            message.success("Xo thnh cng!");
        }).catch(err => {
            message.error("C li xy ra!");
        });
    };
    edituserRole = (params) => {
        const url = `/userRoles/${params.idEdit}`;
        return axiosClient.patch(url, params).then(data => {
            message.success("Sa thnh cng!");
        }).catch(err => {
            message.error("C li xy ra!");
        });
    }
}
const userRoleApi = new UserRoleApi();
export default userRoleApi;